<template>
  <div class="customerVip">
    <div class="log">
      <!-- 登录管理 -->
      <div class="login" style="margin-top: 10px;">
        <img src="../../assets/u19.png" class="img1" />
        <span style="color:#fff;font-size: 28px;">
          和智云仓储管理系统
          <br />HeZi Cloud Warehouse Management System
        </span>
        <el-form
          :model="ruleForm2"
          :rules="rules2"
          ref="ruleForm2"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item prop="name">
            <i
              class="fa fa-user-circle-o"
              aria-hidden="true"
              style="color:#CCCCCC;font-size: 26px;"
            ></i>
            <el-input
              type="text"
              v-model="ruleForm2.name"
              auto-complete="off"
              style="width: 95%;margin-bottom: 12px;"
              placeholder="请输入用户名称"
            ></el-input>
          </el-form-item>
          <el-form-item prop="pass">
            <i
              aria-hidden="true"
              class="fa fa-lock"
              style="font-size: 30px;margin-right: 4px;color:#CCCCCC;"
            ></i>
            <el-input
              type="password"
              v-model="ruleForm2.pass"
              @keyup.enter.native="submitForm('ruleForm2')"
              auto-complete="off"
              style="width: 95%;"
              placeholder="请输入登录密码"
            ></el-input>
            <p style="color:#2894FF; font-size: 12px; text-align: center">测试账号：123/123123</p>
          </el-form-item>

          <div class="sub">
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm2')">
                <i class="fa fa-arrow-circle-o-right btn2">登录</i>
              </el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    var validateName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入用户名称"));
      }
      if (value !== "123") {
        callback(new Error("用户不存在！"));
      } else {
        if (this.ruleForm2.pass !== "") {
          this.$refs.ruleForm2.validateField("pass");
        }
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入登录密码"));
      } else if (value !== "123123") {
        callback(new Error("登录密码错误!"));
      } else {
        callback();
      }
    };
    const validatePass1 = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error("密码不能小于6位"));
      } else {
        callback();
      }
    };
    return {
      ruleForm2: {
        name: "",
        pass: ""
      },
      rules2: {
        name: [{ validator: validateName, trigger: "blur" }],
        pass: [
          { required: true, trigger: "blur", validator: validatePass1 },
          { validator: validatePass, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$router.push("/index/house/allocation");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>
<style >
.img1 {
  border-width: 0px;
  position: absolute;
  left: -65px;
  top: 0px;
  width: 65px;
  height: 65px;
}
.btn2 {
  width: 200px;
  /* height: 200px; */
}
.customerVip {
  width: 100%;
  position: absolute;
  background: url("./../../assets/login.jpg");
}
.customerVip .log {
  background: #374349;
  height: 364px;
  margin: 10% 0;
  padding-top: 10px;
}
.customerVip .login {
  position: relative;
  margin: auto 30%;
}
.customerVip .login .el-form-item__content {
  margin-left: 0px !important;
}
.customerVip .login .el-form {
  margin-top: 43px;
}
.customerVip .sub .el-form-item {
  margin-top: 28px;
  text-align: center;
}
.customerVip .login .el-form-item__label {
  padding: 8px 12px 11px 0;
}
.customerVip .login .el-form-item__error {
  margin-left: 29px;
}
.customerVip .login .el-form-item.is-required .el-form-item__label:before {
  content: "";
}
.el-form-item__error{
  top: 80%
}
</style>


