<template>
  <div class="inContainer">
    <div class="inbox">
      <div class="gys-list">
        <div class="layoutgr">
          <el-form>
            <el-form-item label="入库单号:">
              <el-input placeholder="请输入/扫描单号"></el-input>
            </el-form-item>
            <el-form-item label="单据类型:">
              <el-select v-model="value" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="入库单状态： ">
              <el-select v-model="value2" placeholder="请选择">
                <el-option
                  v-for="item in options2"
                  :key="item.value2"
                  :label="item.label2"
                  :value="item.value2"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-button type="primary" class="fa fa-search">查询</el-button>
            <el-button plain class="fa fa-undo">重置</el-button>
          </el-form>
        </div>
      </div>

      <div class="grid">
        <div class="layoutitem" style="width:100%;border:none;">
          <div>
            <span>数据列表</span>
            <div class="fy2">
              <el-button plain class="fa fa-plus">新增</el-button>
              <el-button plain class="fa fa-pencil">编辑</el-button>
              <el-button plain class="fa fa-trash-o">删除</el-button>
              <el-button plain class="fa fa-refresh">刷新</el-button>
              <el-button plain class="fa fa-upload">导出</el-button>
            </div>
          </div>
          <el-table
            ref="multipleTable"
            height="500"
            :data="IntableList"
            border
            style="width: 100%"
            :default-sort="{prop: 'ID', order: 'ascending'}"
          >
            <el-table-column type="selection"></el-table-column>
            <el-table-column prop="ID" label="订单号" width="80" sortable></el-table-column>
            <el-table-column prop="number" label="产品名称" width="100" sortable></el-table-column>
            <el-table-column prop="spbh" label="产品编号" width="100" sortable></el-table-column>
            <el-table-column prop="name" label="批次" width="95" sortable></el-table-column>
            <el-table-column prop="ck" label="规格" width="60" sortable></el-table-column>
            <el-table-column prop="quantity" label="数量" width="60" sortable></el-table-column>
            <el-table-column prop="dw" label="库位" width="110" sortable></el-table-column>
            <el-table-column prop="rkdj" label="入库类型" width="100" sortable></el-table-column>
            <el-table-column prop="rkje" label="供应商" width="110" sortable></el-table-column>
            <el-table-column prop="bz" label="制单人" width="90" sortable></el-table-column>
            <el-table-column prop="bz" label="状态" width="100" sortable></el-table-column>
            <el-table-column prop="bz" label="审核人" width="80"></el-table-column>
            <el-table-column prop="bz" label="审核时间" width="100"></el-table-column>
            <el-table-column label="操作" width="200" fixed="right">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="handleEdit(slot-scope.$index, scope.row)"
                  class="fa fa-arrow-circle-right"
                >查看</el-button>
                <el-button
                  size="mini"
                  @click="handleEdit(slot-scope.$index, scope.row)"
                  class="fa fa-pencil"
                >编辑</el-button>
                <el-button
                  size="mini"
                  @click.native.prevent="handdeleteRow(slot-scope.$index, IntableList)"
                  class="fa fa-trash"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="di">
            <div>
              <el-checkbox v-model="qx1" @change="qx(IntableList)" label="全选" border></el-checkbox>
              <el-checkbox v-model="fx1" @change="fx(IntableList)" label="反选" border></el-checkbox>
            </div>
            <div>
              <el-select v-model="value" disabled placeholder="批量操作"></el-select>
            </div>
            <div class="fy">
              <el-pagination :page-sizes="[10, 20, 30, 40]" :page-size="50" :total="500"></el-pagination>
            </div>
          </div>
        </div>
      </div>

      <el-dialog title="添加基本信息" :visible.sync="dialogCreatVisible">
        <el-form
          class="small-space"
          :model="temp"
          label-position="right"
          label-width="70px"
          style="width: 400px; margin-left:50px;"
        >
          <el-form-item label="编号">
            <el-input v-model="temp.number"></el-input>
          </el-form-item>

          <el-form-item label="ID">
            <el-input v-model="temp.ID"></el-input>
          </el-form-item>

          <el-form-item label="商品编号">
            <el-input v-model="temp.spbh"></el-input>
          </el-form-item>

          <el-form-item label="商品名称">
            <el-input v-model="temp.name"></el-input>
          </el-form-item>
          <el-form-item label="单位">
            <el-input v-model="temp.dw"></el-input>
          </el-form-item>

          <el-form-item label="仓库">
            <el-input v-model="temp.ck"></el-input>
          </el-form-item>
          <el-form-item label="数量">
            <el-input v-model="temp.quantity"></el-input>
          </el-form-item>
          <el-form-item label="入库单价">
            <el-input v-model="temp.rkdj"></el-input>
          </el-form-item>
          <el-form-item label="入库金额">
            <el-input v-model="temp.rkje"></el-input>
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="temp.bz"></el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogCreatVisible = false">取 消</el-button>

          <el-button type="primary" @click="handleCreateSubmit">确 定</el-button>
        </div>
      </el-dialog>
      <el-dialog title="编辑信息" :visible.sync="dialogEditVisible">
        <el-form
          class="small-space"
          :model="temp"
          label-position="right"
          label-width="70px"
          style="width: 400px; margin-left:50px;"
        >
          <el-form-item label="编号">
            <el-input v-model="temp.number"></el-input>
          </el-form-item>

          <el-form-item label="ID">
            <el-input v-model="temp.ID"></el-input>
          </el-form-item>

          <el-form-item label="商品编号">
            <el-input v-model="temp.spbh"></el-input>
          </el-form-item>

          <el-form-item label="商品名称">
            <el-input v-model="temp.name"></el-input>
          </el-form-item>
          <el-form-item label="单位">
            <el-input v-model="temp.dw"></el-input>
          </el-form-item>

          <el-form-item label="仓库">
            <el-input v-model="temp.ck"></el-input>
          </el-form-item>
          <el-form-item label="数量">
            <el-input v-model="temp.quantity"></el-input>
          </el-form-item>
          <el-form-item label="入库单价">
            <el-input v-model="temp.rkdj"></el-input>
          </el-form-item>
          <el-form-item label="入库金额">
            <el-input v-model="temp.rkje"></el-input>
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="temp.bz"></el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogEditVisible = false">取 消</el-button>

          <el-button type="primary" @click="handleEditSubmit">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { api } from "@/global/api";
import Vue from "vue";
export default {
  data() {
    return {
      qx1: false,
      fx1: false,
      options: [
        {
          value: "选项1",
          label: " 采购收货入库"
        },
        {
          value: "选项2",
          label: "销售退货入库"
        },
        {
          value: "选项3",
          label: "生产产品入库"
        },
        {
          value: "选项4",
          label: "领用退还入库"
        },
        {
          value: "选项5",
          label: "借货入库"
        },
        {
          value: "选项6",
          label: "借出还入"
        }
      ],
      options2: [
        {
          value2: "选项1",
          label2: " 全部"
        },
        {
          value2: "选项2",
          label2: "待审核"
        },
        {
          value2: "选项3",
          label2: "审核成功"
        },
        {
          value2: "选项4",
          label2: "审核失败"
        }
      ],
      value2: "",
      value: "",
      value1: "",
      dialogCreatVisible: false,
      dialogEditVisible: false,
      isShow: false,
      checkList: [],
      IntableList: [],
      temp: {
        number: "",
        ID: 22,
        spbh: "",
        name: "",
        dw: "",
        ck: "",
        quantity: "",
        rkdj: "",
        rkje: "",
        bz: ""
      }
    };
  },
  mounted() {
    var vm = this;
    vm.getData();
  },
  methods: {
    getData() {
      this.$http.get(api.kfData).then(
        function(response) {
          console.log(response);
          console.log("这是我们需要的json数据", response.data.IntableData);
          this.IntableList = response.data.IntableData;
        },
        function(response) {
          alert("请求失败了");
        }
      );
    },
    handleCreate() {
      this.dialogCreatVisible = true;
    },
    handleEdit() {
      this.dialogEditVisible = true;
    },
    qx() {
      //全选
      this.fx1 = false;
      if (this.$refs.multipleTable.selection.length < this.IntableList.length) {
        this.qx1 = true;
      } else {
        this.qx1 = false;
      }
      if (this.qx1) {
        this.IntableList.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row, true);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
        this.selectedAssetsList = [];
        this.assetsUidList = [];
      }
    },
    fx(rows) {
      //反选
      this.qx1 = false;
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    //新增提交
    handleCreateSubmit() {
      var vm = this;
      var flag = 0;
      for (var intablelist of vm.IntableList) {
        console.log("进入循环flag", flag);
        if (vm.temp.ID != intablelist.ID) {
          console.log("不相等的时候ID", intablelist.ID);
          flag++;
        }
      }
      console.log("退出if后flag", flag);
      if (flag == vm.IntableList.length) {
        vm.IntableList.push(vm.temp);
        vm.temp = JSON.parse(JSON.stringify(vm.temp));
      } else {
        alert("ID重复了,请重新输入");
      }
      this.dialogCreatVisible = false;
    },
    //编辑提交
    handleEditSubmit() {
      var vm = this;
      var flag = 0;
      for (var intablelist of vm.IntableList) {
        if (vm.temp.ID == intablelist.ID) {
          var index = vm.IntableList.indexOf(intablelist);
          console.log("当前索引号", index);
          Vue.set(vm.IntableList, index, vm.temp);
          vm.temp = JSON.parse(JSON.stringify(vm.temp));
          flag++;
        }
      }
      if (flag == 0) {
        alert("这个ID不存在，请重新输入");
      }
      this.dialogEditVisible = false;
    },
    // 删除单条
    handdeleteRow(index, rows) {
      rows.splice(index, 1);
    }
  }
};
</script>
<style>
.fy2 {
  float: right;
  margin-bottom: 1%;
}
.fy {
  float: right;
}
.di > div {
  margin-top: 1%;
  display: inline-block;
}
.inContainer {
  float: left;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.inbox {
  padding: 20px 24px;
}
.con-header {
  width: 100%;
  overflow: hidden;
}
.header-rg {
  float: right;
  padding-right: 25px;
  display: block;
}
.inbox .el-form-item {
  float: left;
  padding-right: 8px;
}
.inbox .el-form-item .el-form-item__content {
  float: left;
}
.header-rg .el-input__inner {
  height: 30px;
  width: 120px;
}
.layoutgr {
  padding-left: 20px;
}
.inbox .gys-list .layoutgr .el-form-item {
  float: left;
  padding-right: 40px;
  padding-bottom: 5px;
  margin-bottom: 0;
}
.layoutgr {
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
  overflow: hidden;
}
.inbox .gys-list .layoutgr .el-form-item__label {
  width: inherit;
}
.layoutgr .el-input__inner {
  height: 30px;
  width: 160px;
}
.cellspacing {
  margin-top: 10px;
}
.cell-zd {
  float: left;
  display: inline-block;
}
.cell-je {
  float: right;
  padding-right: 12px;
}
.small-space .el-form-item {
  margin-bottom: 10px;
}
.cell-jespan {
  color: white;
}
.inbox .el-table--enable-row-hover .el-table__body tr:hover > td {
  background-color: white;
  background-clip: padding-box;
}
.inbox .el-input__icon {
  right: 70px;
}
.inbox .el-dialog__footer {
  clear: both;
}
.el-table .cell,
.el-table th > div {
  padding-left: 5px;
  padding-right: 5px;
}
</style>


